
.billet{
margin-bottom:300px;

.title{
    margin:0;
    color:white;
    background:rgb(41, 41, 41);
    padding:20px;
    font-size:24pt;
}

h3{
    font-size:24pt;
}

.billetgrid{
    padding:20px;
    box-sizing:border-box;
    padding-top:2%;
    width:1366px;
    display:grid;
    margin:auto;
    max-width:100%;
    grid-gap:2%;
    grid-template-columns: 1fr;
    box-sizing: border-box;
        .gridItem{
        background:#f4f4f4;
            p{
                margin:0;
            }

            button{
            margin:0;
            width:30%;
            padding:10px;
            }

            .ticketgrid{
                display:grid;
                grid-template-columns: 4fr 3fr 2fr;
                padding:10px;
                padding-left:40px;
                border-bottom:1px solid black;
                button{
                    width:80%;
                    padding:auto;
                    margin:auto auto;
                    outline:none;
                    border:none;
                    color:white;
                    font-size:22pt;
                    padding:18px;
                    background:rgb(156, 48, 48);
                }
            }
        }
    }
}